import React from 'react';
import styles from "./instruction.module.css";
import {Block} from "../../Block";

export function Instruction() {
    return (
        <div className={styles.block}>
            <Block title='Инструкция'>
                <p>В начале каждого дня выбирайте задачи для выполнения и заносите их в список планирования выше.</p>
                <p>Начинайте работать:</p>
                <ol>
                    <li>Запустите таймер («помидор»)</li>
                    <li>Работайте пока «помидор» не прозвонит</li>
                    <li>Сделайте короткий перерыв (3-5 минут)</li>
                </ol>
                <p>Продолжайте работать «помидор» за «помидором», пока все задачи не будут выполнены. Каждые 4 «помидора» делайте длинный перерыв (15-30 минут).</p>
            </Block>
        </div>
    );
}
